import React from 'react'
import { Helmet } from 'react-helmet'
import OTOContent from '../components/OTOContent'
import OfferSelection from '../components/OfferSelection'

/**
 * OTO Page Template
 * 
 * Reusable template for OTO (One-Time Offer) pages that handles all boilerplate:
 * - Layout wrapper (provided by consuming project)
 * - Helmet/SEO configuration
 * - Test mode banner
 * - OTOContent component integration
 * - OfferSelection component integration
 * 
 * @param {Object} Layout - Layout component from consuming project (required)
 * @param {Object} offerData - OTO configuration object (required)
 * @param {string} checkoutUrl - URL to checkout page (required)
 * @param {string} declineUrl - URL when declining offer (required)
 * @param {React.ReactNode} productImage - Product image component (optional)
 */
const OTOPageTemplate = ({ 
  Layout, 
  offerData, 
  checkoutUrl, 
  declineUrl, 
  productImage 
}) => {
  // Validate required props
  if (!Layout) {
    throw new Error('OTOPageTemplate requires Layout prop')
  }
  if (!offerData) {
    throw new Error('OTOPageTemplate requires offerData prop')
  }
  if (!checkoutUrl) {
    throw new Error('OTOPageTemplate requires checkoutUrl prop')
  }
  if (!declineUrl) {
    throw new Error('OTOPageTemplate requires declineUrl prop')
  }

  // Check if we're in test mode
  const isTest = process.env.GATSBY_ISTEST === 'true'
  
  // Add TEST indicator to button text if in test mode
  const primaryButtonText = offerData.buttons?.primary_text || "Yes, I Want This"
  const displayPrimaryText = isTest ? `${primaryButtonText} (TEST)` : primaryButtonText

  return (
    <Layout pageTitle={offerData.meta.title}>
      <Helmet>
        <title>{offerData.meta.title}</title>
        <meta name="description" content={offerData.meta.description} />
        <meta name="keywords" content={offerData.meta.keywords} />
        <meta name="author" content={offerData.meta.author} />
        <meta name="robots" content="noindex, nofollow" />
      </Helmet>

      {/* Test Mode Banner */}
      {isTest && (
        <div className="bg-yellow-400 text-black text-center py-2 px-4 font-bold">
          ‚ö†Ô∏è TEST MODE - Using test Stripe keys and test price IDs
        </div>
      )}

      <OTOContent 
        offerData={offerData}
        timerMinutes={offerData.timer_minutes}
        productImage={productImage}
      >
        {/* Offer Selection Buttons */}
        <OfferSelection 
          checkoutUrl={checkoutUrl}
          declineUrl={declineUrl}
          primaryText={displayPrimaryText}
          secondaryText={offerData.buttons?.secondary_text || "No thanks, take me to my purchase"}
        />
      </OTOContent>
    </Layout>
  )
}

export default OTOPageTemplate
